.contact {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 100%;
  height: 80vh;
  background-size: cover;
  overflow: hidden;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.contactSectionTitle {
  text-transform: uppercase;
  position: absolute;
  font-weight: 200;
  font-size: 16px;
  text-align: left;
  padding: 25px 0;
  left: 100px;
  right: 0;
  line-height: 70px;
  letter-spacing: 2px;
  height: 70px;
  background: -webkit-gradient(linear, left top, left bottom, from(#07070a), to(transparent));
  background: -webkit-linear-gradient(#07070a, transparent);
  background: -o-linear-gradient(#07070a, transparent);
  background: linear-gradient(#07070a, transparent)
}

input[type=text], 
select,
input[type=email] {
  width: 80%;
  min-width: 280px;
  padding: 1.5vh 0;
  margin: 1.5vh 0;
  text-align: left;
  background: transparent;
  display: inline-block;
  border-top: none;
  color: #ffffff;
  border-right: none;
  border-left: none;
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  outline: none; 
  -webkit-box-sizing: border-box; 
          box-sizing: border-box;
  -webkit-transition-property: border-bottom;
  -o-transition-property: border-bottom;
  transition-property: border-bottom;
  -webkit-transition-duration: 300ms;
       -o-transition-duration: 300ms;
          transition-duration: 300ms;
}

input[type=text]:focus,
input[type=email]:focus {
  color: #ffffff;
  border-bottom: 1px solid rgb(255, 255, 255);
  -webkit-transition-property: border-bottom;
  -o-transition-property: border-bottom;
  transition-property: border-bottom;
  -webkit-transition-duration: 300ms;
       -o-transition-duration: 300ms;
          transition-duration: 300ms;
}


input[type=submit] {
  width: 100%;
  background-color: transparent;
  color: rgba(255, 255, 255, 0.5);
  margin: 25px 0;
  width: 70px;
  padding: 0;
  text-align: left;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-family: 'Montserrat', sans-serif;
  font-weight: 200;
  font-size: 14px;
  -webkit-transition-property: color;
  -o-transition-property: color;
  transition-property: color;
  -webkit-transition-duration: 300ms;
       -o-transition-duration: 300ms;
          transition-duration: 300ms;
}

input[type=submit]:hover {
  color: rgba(255, 255, 255, 1);
}

form {
  -ms-flex-item-align: center;
      align-self: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.contactForm {
  background-color: transparent;
  position: absolute;
  width: 60%;
  font-family: 'Montserrat', sans-serif;
  font-weight: 200;
  padding: 20vh 0;
  left: 100px;
}

.contactFormHeader {
  cursor: default;
  margin: 4vh 0;
  font-weight: 200;
  font-size: 16px;
  letter-spacing: 1px;
  line-height: 24px;
  text-align: left;
}


