.portfolio {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 100%;
  height: 100vh;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  overflow: hidden;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.portfolioSectionTitle {
  text-transform: uppercase;
  position: absolute;
  font-weight: 200;
  font-size: 16px;
  text-align: left;
  padding: 25px 0;
  left: 100px;
  right: 0;
  line-height: 70px;
  letter-spacing: 2px;
  height: 70px;
}

.portfolioDisplay {
  display: -ms-flexbox;
  display: flex;
  z-index: 1;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -ms-flex-direction: row;
      flex-direction: row;
  width: 100%;
  min-width: 800px;
  height: 100%;
  padding: 0 5vw;
}

.projectMenu {
  -ms-flex: 1 1;
      flex: 1 1;
  padding: 15vh 30px 0 0;
  line-height: 40px;
  position: absolute;
  font-family: 'Montserrat', sans-serif;
  font-weight: 200;
  left: 100px;
}

.projectMenuItem {
  display: inline-block;
  -o-text-overflow: clip;
     text-overflow: clip;
  text-transform: uppercase;
  font-weight: 900;
  margin: 35px 0;
  font-size: 82px;
  letter-spacing: 1px;
  cursor: pointer;
}

.projectMenuItemDivider {
  display: inline-block;
  -ms-flex-pack: center;
      justify-content: center;
  -webkit-transform: translateY(20px);
      -ms-transform: translateY(20px);
          transform: translateY(20px);
  width: 150px;
  height: 100px;
  margin: none;
  padding: 49px 25px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  cursor: default;
}

.projectMenuItemDivider-white-space {
  height: 2px;
  width: 100%;
  background: #ffffff;
  cursor: default;
}

.questBackground,
.railsightBackground,
.richBackground,
.proximityBackground,
.uproarBackground,
.questBackground-visible,
.railsightBackground-visible,
.richBackground-visible,
.proximityBackground-visible,
.uproarBackground-visible {
  position: absolute;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
  width: 100vw;
  height: 100vh;
  z-index: 0;
  opacity: 0;
  -webkit-transition-property: -webkit-transform opacity;
  transition-property: -webkit-transform opacity;
  -o-transition-property: transform opacity;
  transition-property: transform opacity;
  transition-property: transform opacity, -webkit-transform opacity;
  -webkit-transition-duration: 350ms;
       -o-transition-duration: 350ms;
          transition-duration: 350ms;
}

.questBackground-visible,
.railsightBackground-visible,
.richBackground-visible,
.proximityBackground-visible,
.uproarBackground-visible {
  opacity: 1;
  -webkit-transform: scaleX(1);
      -ms-transform: scaleX(1);
          transform: scaleX(1);
  -webkit-transition-property: -webkit-transform opacity;
  transition-property: -webkit-transform opacity;
  -o-transition-property: transform opacity;
  transition-property: transform opacity;
  transition-property: transform opacity, -webkit-transform opacity;
  -webkit-transition-duration: 400ms;
       -o-transition-duration: 400ms;
          transition-duration: 400ms;
}


@-webkit-keyframes backgroundChange {
  0% {
    background-size: 120%;
    -webkit-background-size: 120%;
  }
  100% {
    background-size: 100% contain;
    -webkit-background-size: 100%;
  }
}


@keyframes backgroundChange {
  0% {
    background-size: 120%;
    -webkit-background-size: 120%;
  }
  100% {
    background-size: 100% contain;
    -webkit-background-size: 100%;
  }
}


